.tag-bubble {
    display: inline-block;
    background-color: #eee;
    margin: 2px 3px;
    padding: 5px;
    border-radius:999px;
    -webkit-transition: all 0.2s ease-in-out;
    -o-transition: all 0.2s ease-in-out;
    transition: all 0.2s ease-in-out;
}
.tag-bubble:hover {
    background-color: #d3d3d3;
}


